import { CalendarType } from './calendarType';
import { CalendarLang } from './calendarLang';
import { formatDateString, getAudibleDateString, getDayOfWeek } from './calendarUtils';

const today = new Date();

interface CalendarDateProps {
    selected: boolean;
    lang: CalendarLang;
    prev: boolean;
    next: boolean;
}

const defaultProps: CalendarDateProps = {
    selected: false,
    lang: CalendarLang.Sv,
    prev: false,
    next: false,
};

export class CalendarDate {
    // Private members
    private readonly _date: Date;
    private readonly _props: CalendarDateProps;
    private readonly _cache: any = {};

    // Public methods
    constructor(date: Date, props?: Partial<CalendarDateProps>) {
        this._date = new Date(date);
        this._props = { ...defaultProps, ...props };
    }

    isPrev() {
        return this._props.prev;
    }

    isNext() {
        return this._props.next;
    }

    isSelected() {
        return this._props.selected;
    }

    getYear(): number {
        return this._date.getFullYear();
    }

    getYearString(): string {
        return this.getYear().toString();
    }

    getDate(): number {
        return this._date.getDate();
    }

    getDateString(): string {
        return this.getDate().toString();
    }

    getMonth(): number {
        return this._date.getMonth();
    }

    getMonthString(): string {
        return this.getMonth().toString();
    }

    getDayOfWeek(): number {
        return getDayOfWeek(this._date);
    }

    isWeekend(): boolean {
        if (typeof this._cache.isWeekend === 'undefined') {
            const dayOfWeek = this.getDayOfWeek();
            this._cache.isWeekend = dayOfWeek >= 5;
        }

        return this._cache.isWeekend;
    }

    isToday(): boolean {
        if (typeof this._cache.isToday === 'undefined') {
            const result =
                formatDateString(this._date, CalendarType.Date) === formatDateString(today, CalendarType.Date);
            this._cache.isToday = result;
        }

        return this._cache.isToday;
    }

    format(type: CalendarType, audible = false) {
        const date = this._date;

        if (audible) {
            return getAudibleDateString(date, type, this._props.lang);
        }

        return formatDateString(date, type);
    }
}
