import {
    baseInputStyle,
    css,
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
} from '@umetrics/sartorius-ui-base-input';

export const datePickerStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        baseInputStyle(tagName),
        css`
            :host {
                display: block;
            }

            ${root} {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
                width: fit-content;
                min-width: 80px;
            }

            ${child('outer')} {
                display: inline-flex;
                overflow: hidden;
                position: relative;
                align-items: center;
                height: 40px;
                min-width: 0;
                width: 100%;
                margin-right: 14px;
                background-color: ${cssVariableAsVar('color_primary_2')};
            }

            ${child('outer')}:after {
                content: ' ';
                display: block;
                position: absolute;
                border: 1px solid ${cssVariableAsVar('color_secondary_3')};
                inset: 0;
                z-index: 0;
                pointer-events: none;
            }

            ${is('focused')} ${child('outer')}:after {
                border: 1px solid ${cssVariableAsVar('color_primary_3')};
                z-index: 1;
            }

            ${is('validation')} ${child('outer')}:after {
                border: 1px solid ${cssVariableAsVar('color_traffic_light_1')};
                z-index: 1;
            }

            ${is('start')} ${child('outer')}:after {
                border-right: 0;
            }

            ${is('end')} ${child('outer')}:after {
                border-left: 0;
            }

            ${is('!focused', '!disabled', 'hover')} ${child('outer')}:after {
                border-top: 1px solid ${cssVariableAsVar('color_secondary_6')};
                border-bottom: 1px solid ${cssVariableAsVar('color_secondary_6')};
                z-index: 1;
            }

            ${is('!focused', '!disabled', '!start', 'hover')} ${child('outer')}:after {
                border-right: 1px solid ${cssVariableAsVar('color_secondary_6')};
            }

            ${is('!focused', '!disabled', '!end', 'hover')} ${child('outer')}:after {
                border-left: 1px solid ${cssVariableAsVar('color_secondary_6')};
            }

            ${is('disabled')} ${child('outer')} {
                outline: none;
                background-color: ${cssVariableAsVar('color_secondary_2')};
            }

            ${child('icon')} {
                margin: 0 14px;
            }

            ${child('divider')} {
                pointer-events: none;
                padding: 0 2px;
                font-size: 14px;
                color: ${cssVariableAsVar('color_secondary_7')};
            }

            ${child('dash')} {
                margin-right: 14px;
            }

            ${child('wrapper')} {
                position: relative;
                height: 100%;
                font-size: 14px;
            }

            ${child('inputs')} {
                border: none;
                padding: 0;
                letter-spacing: 0;
                text-align: left;
                width: 100%;
                height: 100%;
                position: absolute;
                left: 0;
                color: var(--color-primary-3);

                -moz-appearance: textfield;
            }

            ${child('width')} {
                visibility: hidden;
            }

            ${child('inputs')}::-webkit-inner-spin-button {
                display: none;
            }

            ${child('inputs')}:focus {
                outline: 0;
            }

            ${child('input-row')} {
                position: relative;
                display: flex;
                flex-direction: column;
            }

            ${child('calendar')} {
                visibility: visible;
                opacity: 1;
                transform-origin: var(--calendar-transform-origin, top left);
                transform: scale(1);
                transition:
                    visibility 0s linear 300ms,
                    height 0s,
                    opacity 300ms,
                    transform 300ms;
            }

            ${is('pos-fixed')} ${child('calendar')} {
                position: fixed;
                z-index: 10;
                top: var(--calendar-top, 0);
                left: var(--calendar-left, 0);
            }

            ${is('pos-fixed-auto')} ${child('calendar')} {
                position: fixed;
                z-index: 10;
                top: var(--calendar-top, 0);
                left: var(--calendar-left, 0);
            }

            ${is('pos-absolute')} ${child('calendar')} {
                position: absolute;
                top: 100%;
                z-index: 10;
            }

            ${is('pos-none')} ${child('calendar')} {
                display: none;
            }

            ${is('!show')} ${child('calendar')} {
                visibility: hidden;
                opacity: 0;
                transform: scale(0);
            }

            ${child('feedback-wrapper')} {
                display: flex;
            }
        `,
    ] as CSSResultArray;
};
