import { CalendarType } from './calendarType';
import { daysByLang, monthsByLang } from './calendarStrings';
import { CalendarLang } from './calendarLang';
import { CalendarCustomizeDate } from './calendarCustomizeDate';
import { CalendarDate } from './calendarDate';

export const getDayOfWeek = (date: Date): number => {
    let n = date.getDay() - 1;
    if (n === -1) {
        // Make sure Monday is first day of week instead of Sunday.
        n = 6;
    }
    return n;
};

// Private functions
function _padNumber(value: number, width = 1) {
    width -= value.toString().length;
    if (width > 0) {
        return new Array(width + (/\./.test(value.toString()) ? 2 : 1)).join('0') + value;
    }
    return value.toString();
}

function containsLetters(value): boolean {
    return /[a-zA-Z]+/.test(value);
}

export function containsCentury(str: string, type: CalendarType): boolean {
    const lengthWithCentury = type === CalendarType.Month ? 6 : 8;
    if (str.length > 4) {
        return str.length % lengthWithCentury === 0;
    } else if (str.length === 4 && str.indexOf('-') < 0) {
        return true;
    }
    return false;
}

const checkFallbackValue = (value) => {
    if (!value) {
        return true;
    }
    return containsLetters(value);
};
const checkValue = (value, type) => {
    if (type === CalendarType.Month) {
        if (/^\d{4}-\d{2}$/.test(value)) {
            return true;
        }
    } else if (/^\d{4}-\d{2}-\d{2}$/.test(value)) {
        return true;
    }
    return false;
};
const splitDate = (value, type, currentCentury) => {
    let split = value.split(/\D/).slice(0, 3).filter(Boolean);

    if (split.length === 0) {
        return undefined;
    } else if (split.length === 1) {
        let v = split[0];
        if (v.length === 1) {
            v = '0' + v;
        }
        const hasCentury = containsCentury(v, type);
        const offset = hasCentury ? 2 : 0;
        const yyyy = (!hasCentury ? currentCentury : '') + v.substring(0, offset + 2);
        const mm = v.substring(offset + 2, offset + 4) || '1';
        const dd = v.substring(offset + 4, offset + 6) || '1';
        split = [yyyy, mm, dd];
    } else if (split.length === 2) {
        const yyyy = split[0];
        const mm = split[1].substring(0, 2);
        const dd = split[1].substring(2, 4) || '1';
        split = [yyyy, mm, dd];
    }
    return split;
};

export const ensureValidFormat = (value = '', type: CalendarType = CalendarType.Date): string => {
    value = value.trim();
    const fallbackValue = '';

    if (checkFallbackValue(value)) {
        return fallbackValue;
    }
    if (checkValue(value, type)) {
        return value;
    }
    const currentYear = new Date().getFullYear().toString();
    const currentCentury = currentYear.substring(0, 2);
    const split = splitDate(value, type, currentCentury);
    if (!split) {
        return fallbackValue;
    }
    let mapped = split.map((v, i) => {
        if (i === 0) {
            return v.length === 2 ? currentCentury + v : _padNumber(parseInt(v, 10), 4);
        }
        return _padNumber(parseInt(v, 10), 2);
    });
    if (type === CalendarType.Month) {
        mapped = mapped.slice(0, 2);
    }
    return mapped.join('-');
};

export const letterFormatToValidFormat = (value: string, lang: string) => {
    value = value.trim();
    const split = value.split(' ');
    const yyyy = split[2] ? _padNumber(parseInt(split[2], 10), 4) : new Date().getFullYear().toString();
    let mm = '01';
    const dd = split[0] ? _padNumber(parseInt(split[0], 10), 2) : '01';

    for (let i = 0; i < monthsByLang[lang].length; i++) {
        const v = monthsByLang[lang][i];
        if (v && v.toLowerCase().indexOf(split[1].toLowerCase()) === 0) {
            mm = _padNumber(i + 1, 2);
        }
    }

    return `${yyyy}-${mm}-${dd}`;
};

export const getWeekForDate = (date: Date) => {
    const d = new Date(Date.UTC(date.getFullYear(), date.getMonth(), date.getDate()));
    const dayNum = d.getUTCDay() || 7;
    d.setUTCMilliseconds(0);
    d.setUTCDate(d.getUTCDate() + 4 - dayNum);
    const yearStart = new Date(Date.UTC(d.getUTCFullYear(), 0, 1));
    return Math.ceil(((d.getTime() - yearStart.getTime()) / 86400000 + 1) / 7);
};

export const getMonthString = (date: Date, lang: CalendarLang): string => {
    const month = date.getMonth();
    return monthsByLang[lang][month] || '';
};

export const getAbbreviatedMonthString = (date: Date, lang: CalendarLang): string => {
    let month = getMonthString(date, lang);
    if (month.length > 4) {
        month = month.substring(0, 3);
    }
    return month;
};

export const isDateDisabled = (date: Date, lang: CalendarLang, customizeDateHandler?: CalendarCustomizeDate) => {
    const custom = date && customizeDateHandler ? customizeDateHandler(new CalendarDate(date, { lang })) : null;
    return custom ? custom.disabled === true : false;
};

export const parseDateString = (dateString: string) => {
    const date = new Date(dateString);

    if (isNaN(date.getTime())) {
        return null;
    }

    return date;
};

export const formatDateString = (date: Date, type: CalendarType): string => {
    const yyyy = date.getFullYear().toString();
    const mm = _padNumber(date.getMonth() + 1, 2);
    const dd = _padNumber(date.getDate(), 2);
    const arr = [yyyy, mm, dd];

    if (type === CalendarType.Month) {
        return arr.slice(0, 2).join('-');
    } else {
        return arr.join('-');
    }
};

export const getAudibleDateString = (date: Date, type: CalendarType, lang: CalendarLang) => {
    const months = monthsByLang[lang];

    if (!months) {
        // Fallback if language is not available
        return formatDateString(date, type);
    }

    const yyyy = date.getFullYear().toString();
    let mm = months[date.getMonth()];

    if (type === CalendarType.Month) {
        return `${mm} ${yyyy}`;
    }

    if (lang === CalendarLang.Sv) {
        mm = mm.toLowerCase();
    }

    const day = daysByLang[lang][getDayOfWeek(date)];
    const dd = date.getDate().toString();
    return `${day[0]} ${dd} ${mm} ${yyyy}`;
};
