import type { BaseInputProperties } from '@umetrics/sartorius-ui-base-input';
import type { DatePickerCalendarPosition } from './datePickerCalendarPosition';
import type { DatePickerCustomizeDate } from './datePickerCustomizeDate';

export enum DatePickerTextKey {
    INVALID = 'invalid',
}

export interface DatePickerTexts {
    [DatePickerTextKey.INVALID]?: string;
}

export interface DatePickerProperties extends BaseInputProperties {
    placeholder?: { year: string; month: string; day: string };
    texts?: DatePickerTexts;
    required?: boolean;
    show?: boolean;
    calendarPosition?: DatePickerCalendarPosition;
    value?: string;
    customizeDate?: DatePickerCustomizeDate;

    /* Props that could be used when implementing period pickers. DatePickerPeriod or custom */
    start?: string;
    end?: string;
    startIsSelectedDate?: boolean;
    endIsSelectedDate?: boolean;

    /* Props to pass state between DatePickers when used in a DatePickerPeriod */
    hover?: boolean;
    focused?: boolean;
    invalid?: string | null;
    periodPickerStyle?: boolean;

    /* Events */
    onChange?: any;
    onInvalid?: any;
}
