import {
    BaseInput,
    booleanConverter,
    createClassName,
    defineElement,
    FeedbackMessageType,
    html,
    property,
    query,
    state,
} from '@umetrics/sartorius-ui-base-input';
import { datePickerStyles } from './datePickerStyle';
import { DatePickerProperties, DatePickerTextKey, DatePickerTexts } from './datePickerProperties';
import { version } from './metadata';
import type { Calendar as CalendarT } from '@umetrics/sartorius-ui-calendar';
import {
    CalendarLang,
    CalendarType,
    ensureValidFormat,
    formatDateString,
    isDateDisabled,
    letterFormatToValidFormat,
} from '@umetrics/sartorius-ui-calendar';
import { DatePickerCalendarPosition } from './datePickerCalendarPosition';
import { DatePickerCustomizeDate } from './datePickerCustomizeDate';
import { DatePickerEvent, DatePickerEventType } from './datePickerEvent';
import calendar from '@umetrics/sartorius-ui-icon/dist/icons/calendar';

@defineElement({
    tagName: 'date-picker',
    styles: datePickerStyles,
    version,
    //sub: [Calendar, FeedbackMessage, Icon]
})
export class DatePicker extends BaseInput<DatePicker> implements DatePickerProperties {
    // Static members
    private static Icon;
    private static Calendar;

    // Private members

    // Protected members

    // Public members
    @property({ type: Object, reflect: false, attribute: false })
    placeholder = { year: 'YYYY', month: 'MM', day: 'DD' };
    @property({ type: Boolean, converter: booleanConverter, reflect: false })
    required = false;
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    show = false;
    @property({ type: String, reflect: false, attribute: 'calendar-position' })
    calendarPosition = DatePickerCalendarPosition.Absolute;
    @property({ type: Function })
    customizeDate?: DatePickerCustomizeDate;
    @property({ type: Boolean, attribute: 'start-is-selected-date', converter: booleanConverter })
    startIsSelectedDate = false;
    @property({ type: Boolean, attribute: 'end-is-selected-date', converter: booleanConverter })
    endIsSelectedDate = false;

    @property({ type: String, reflect: true })
    public get value() {
        return this._inputValue;
    }

    public set value(value: string) {
        const prevValue = this.value;

        if (value === prevValue) {
            return;
        }

        let formatted: string;
        if (this.lang && /^(\d+) (\D*) ?(\d+)?$/.test(value)) {
            // Interpret value as "letter" format (i.e. "1 januari 2019")
            formatted = letterFormatToValidFormat(value, this.lang);
        } else {
            formatted = ensureValidFormat(value, CalendarType.Date);
        }

        value = formatted || value || '';

        this._inputValue = value;
        this._validateSetValue();

        this.requestUpdate('value', prevValue);
    }

    @property({ type: String })
    get start() {
        return this._start;
    }

    set start(value) {
        const prevValue = this.start;

        if (prevValue === value) {
            return;
        }
        const v = ensureValidFormat(value, CalendarType.Date);
        const date = v ? new Date(v) : null;
        if (date && this._isDateValid(date)) {
            this._start = v;
            this._calendarNow = date;
        } else {
            this._start = '';
        }
        this.requestUpdate('start', prevValue);
    }

    @property({ type: String })
    get end() {
        return this._end;
    }

    set end(value) {
        const prevValue = this.end;
        if (prevValue === value) {
            return;
        }
        const v = ensureValidFormat(value, CalendarType.Date);
        const date = v ? new Date(v) : null;
        if (date && this._isDateValid(date)) {
            this._end = v;
            this._calendarNow = date;
        } else {
            this._end = '';
        }
        this.requestUpdate('end', prevValue);
    }

    @property({ type: Object, attribute: true, reflect: false })
    get texts() {
        return this._texts;
    }

    set texts(value) {
        const prevValue = this._texts;
        if (typeof value === 'string') {
            value = JSON.parse(value);
        }
        this._texts = value;
        this.requestUpdate('texts', prevValue);
    }

    /* Props to pass state between DatePickers when used in a DatePickerPeriod */
    @property({ type: Boolean, reflect: true })
    hover = false;
    @property({ type: Boolean, reflect: true })
    focused = false;
    @property({ type: String, reflect: true })
    invalid: string | null = null;
    @property({ type: Boolean, reflect: false, attribute: false })
    periodPickerStyle = false;

    // Private members
    private _inputValue = '';
    @state()
    private _calenderFocused = false;
    @query('[class*="-outer"]')
    private _inputElement!: HTMLDivElement;
    @query('[class*="-calendar"]')
    private _calendar!: CalendarT;
    private _selectedDate: null | Date = null;
    private _activeElement: Element | null = null;
    @state()
    private _calendarNow: null | Date = null;
    private _start = '';
    private _end = '';
    @state()
    private _show = false;
    private _lang: CalendarLang = CalendarLang.En;
    private _texts: DatePickerTexts = { [DatePickerTextKey.INVALID]: 'Invalid date format' };

    @state()
    private _year;
    @state()
    private _month;
    @state()
    private _day;
    @state()
    private _fixedPosition = 'below-left';
    private _eventsAdded = false;

    public get selectedDate() {
        return this._selectedDate;
    }

    // Public methods
    constructor() {
        super();
        this._onInputBlur = this._onInputBlur.bind(this);
        this._onInputFocus = this._onInputFocus.bind(this);
    }

    public focus(options?: FocusOptions) {
        this.shadowRoot!.querySelector('input')!.focus(options);
        this.focused = true;
    }

    public blur() {
        this.shadowRoot!.querySelector('input')!.blur();
        this._show = false;
        this._calendar.style.removeProperty('transform');
        this.focused = false;
        this._calenderFocused = false;
    }

    connectedCallback() {
        super.connectedCallback();
        this._handleResize = this._handleResize.bind(this);
        this._handleScroll = this._handleScroll.bind(this);
    }

    disconnectedCallback() {
        this._removeEventListeners();
        super.disconnectedCallback();
    }

    protected _getChildImports() {
        const imports: any[] = [];
        imports.push({ module: () => import('@umetrics/sartorius-ui-icon'), elem: 'Icon' });
        imports.push({
            module: () => import('@umetrics/sartorius-ui-calendar'),
            elem: [
                'Calendar',
                'CalendarType',
                'ensureValidFormat',
                'formatDateString',
                'isDateDisabled',
                'letterFormatToValidFormat',
            ],
        });
        return imports;
    }

    protected update(changedProperties) {
        super.update(changedProperties);
        const prevShow = changedProperties.get('show');
        const prevInternalShow = changedProperties.get('_show');
        const isVisible = this.show || this._show;
        if ((prevShow !== undefined && this.show) || (prevInternalShow !== undefined && this._show)) {
            this._positionCal();
            this._calendarNow = this._selectedDate ? this._selectedDate : this._calendarNow;
        }
        if (isVisible) {
            this._addEventListeners();
        } else {
            this._removeEventListeners();
        }
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: this.disabled,
            required: this.required,
            focused: this.focused || this._calenderFocused || this._activeElement !== null,
            show: this.show || this._show,
            ['pos-' + this.calendarPosition]: true,
            [this._fixedPosition]: true,
            validation: this.invalid !== null || this.additionalFeedbackType === FeedbackMessageType.Failure,
            start: this.periodPickerStyle && this.endIsSelectedDate,
            end: this.periodPickerStyle && this.startIsSelectedDate,
            hover: this.hover,
        });

        return html`
            <label class="${rootClasses}">
                ${this._renderLabel(baseClassName)}
                <div class="${baseClassName}-input-row" @keydown=${this._onKeyDown}>
                    <div
                        class="${baseClassName}-outer"
                        @mouseenter="${() => (this.hover = true)}"
                        @mouseleave="${() => (this.hover = false)}"
                    >
                        ${this._renderIcon(baseClassName)}
                        ${this._renderVirtualInput(
                            baseClassName,
                            'year',
                            this._year,
                            this._onChangeYear,
                            this.placeholder.year
                        )}
                        ${this._renderDivider(baseClassName)}
                        ${this._renderVirtualInput(
                            baseClassName,
                            'month',
                            this._month,
                            this._onChangeMonth,
                            this.placeholder.month
                        )}
                        ${this._renderDivider(baseClassName)}
                        ${this._renderVirtualInput(
                            baseClassName,
                            'day',
                            this._day,
                            this._onChangeDay,
                            this.placeholder.day
                        )}
                    </div>
                    ${this._renderFeedbackWrapper(baseClassName)} ${this._renderCalendar(baseClassName)}
                </div>
            </label>
        `;
    }

    private _onKeyDown(e: KeyboardEvent) {
        if (e.key === 'Escape') {
            this.show = false;
            (e.target as HTMLInputElement)?.blur();
            e.preventDefault();
        }
    }

    /**
     * Render feedback, if not used in the period picker.
     * @param baseClassName
     */
    private _renderFeedbackWrapper = (baseClassName) => {
        if (this.periodPickerStyle) {
            return html``;
        }
        const text = this.invalid !== null ? this._texts[DatePickerTextKey.INVALID] : '';
        return html`
            <div class="${baseClassName}-feedback-wrapper">${this._renderFeedback(baseClassName, text)}</div>
        `;
    };

    /**
     * Render Icon, if not used in the period picker.
     * @param baseClassName
     */
    private _renderIcon = (baseClassName) =>
        this.periodPickerStyle && this.startIsSelectedDate
            ? html`${this._renderDivider(baseClassName, '—', baseClassName + '-dash')}`
            : html`
                <${DatePicker.Icon.tag} class='${baseClassName}-icon' icon=${calendar}
                                        size='large'></${DatePicker.Icon.tag}>`;

    private _renderVirtualInput = (baseClassName, name, value, changeCallback, placeholder) => html`
        <div class="${baseClassName}-wrapper">
            <span class="${baseClassName}-width">${placeholder}</span>
            <input
                class="${baseClassName}-inputs ${baseClassName}-${name}"
                type="number"
                placeholder="${placeholder}"
                .value="${value}"
                ?required=${!!this.required}
                ?disabled="${!!this.disabled}"
                @input=${(e) => this._onInput(e, placeholder.length)}
                @change=${changeCallback}
                @focus=${this._onInputFocus}
                @blur=${this._onInputBlur}
            />
        </div>
    `;

    private _renderDivider = (baseClassName, char = '/', extraClazz = '') => html`
        <span class="${baseClassName}-divider ${extraClazz}">${char}</span>
    `;

    private _onChangeYear = (e) => {
        this._year = this._onChange(e, this._year, this.placeholder.year.length);
        this._updateInputValue();
    };

    private _onChangeMonth = (e) => {
        this._month = this._onChange(e, this._month, this.placeholder.month.length);
        this._updateInputValue();
    };

    private _onChangeDay = (e) => {
        this._day = this._onChange(e, this._day, this.placeholder.day.length);
        this._updateInputValue();
    };

    private _updateInputValue = () => {
        if (this._year && this._month && this._day) {
            this.value = `${this._year}-${this._month}-${this._day}`;
        } else {
            this.invalid = null;
            this._inputValue = '';
        }
        this._dispatchChangeEvent();
    };

    private _dispatchChangeEvent = () =>
        this.dispatchEvent(
            new DatePickerEvent(DatePickerEventType.Change, {
                detail: {
                    ...(this.invalid ? { invalid: !!this.invalid } : {}),
                    value: this.value,
                    year: this._year ?? '',
                    month: this._month ?? '',
                    day: this._day ?? '',
                },
            })
        );

    private _onChange = (e, prev, size) => {
        const val = e.target.value;
        if (!val) {
            return '';
        } else if (parseInt(val, 10) <= 0) {
            e.target.value = prev;
            return prev;
        }
        return this._pad(val, size);
    };

    private _onInput = (e, size) => {
        e.target.value = e.target.value > size ? e.target.value.slice(0, size) : e.target.value;
    };

    private _renderCalendar(baseClassName) {
        return html`
            <${DatePicker.Calendar.tag}
                class="${baseClassName}-calendar"
                .showOffsetDays=${true}
                .value=${this._selectedDate}
                .now=${this._nowForCalendar()}
                start=${this.start}
                end=${this.end}
                start-is-selected-date=${this.startIsSelectedDate}
                end-is-selected-date=${this.endIsSelectedDate}
                .customizeDate=${this._customizeDateHandler}
                @change=${this._onCalendarChange}
                @focus=${this._onCalendarFocus}
                @blur=${this._onCalendarBlur}
                @pointerdown=${this._onCalendarPointerDown}></${DatePicker.Calendar.tag}>`;
    }

    private _positionCal() {
        if (this.calendarPosition === DatePickerCalendarPosition.Fixed && this._inputElement) {
            const rect = this._inputElement.getBoundingClientRect();
            this._calendar.style.setProperty('--calendar-top', `${rect.y + rect.height}px`);
            this._calendar.style.setProperty('--calendar-left', `${rect.x}px`);
        } else if (this.calendarPosition === DatePickerCalendarPosition.FixedAuto && this._inputElement) {
            // Size is not known since animation based on scale(0), thus hardcoded,
            this._decidePlacement(513, 322);
        }
    }

    private _decidePlacement(containerWidth, containerHeight) {
        const triggerRect = this._inputElement.getBoundingClientRect();
        const {
            width: triggerWidth,
            height: triggerHeight,
            x: triggerX,
            y: triggerY,
            left,
            top,
            right,
            bottom,
        } = triggerRect;
        const { spaceAbove, spaceBelow, spaceToLeft, spaceToRight } = this._calculateSpace(left, top, right, bottom);

        const maxY = triggerY + triggerHeight; // Bottom of the button
        const minX = triggerX; // Left of the button
        const maxX = triggerX + triggerWidth; // Right of the button
        const minY = triggerY; // Top of the button

        const placements = [
            {
                condition: () => spaceBelow > containerHeight && spaceToRight + triggerWidth > containerWidth,
                placement: {
                    top: maxY,
                    left: minX,
                    origin: 'top left',
                },
            },
            {
                condition: () => spaceBelow > containerHeight && spaceToLeft > containerWidth,
                placement: {
                    top: maxY,
                    left: maxX - containerWidth, // Align right edge of dropdown with button right edge
                    origin: 'top right',
                },
            },
            {
                condition: () => spaceToRight > containerWidth && spaceBelow + triggerHeight > containerHeight,
                placement: {
                    top: minY,
                    left: maxX,
                    origin: 'top left',
                },
            },
            {
                condition: () => spaceToLeft > containerWidth && spaceBelow + triggerHeight > containerHeight,
                placement: {
                    top: minY,
                    left: minX - containerWidth, // Align dropdown to the left of the button
                    origin: 'top right',
                },
            },
            {
                condition: () =>
                    spaceToRight > containerWidth && spaceBelow > -1 && spaceAbove + triggerHeight > containerHeight,
                placement: {
                    top: maxY - containerHeight, // Align bottom edge of dropdown with button bottom edge
                    left: maxX,
                    origin: 'bottom left',
                },
            },
            {
                condition: () =>
                    spaceToLeft > containerWidth && spaceBelow > -1 && spaceAbove + triggerHeight > containerHeight,
                placement: {
                    top: maxY - containerHeight, // Align bottom edge of dropdown with button bottom edge
                    left: minX - containerWidth,
                    origin: 'bottom right',
                },
            },
            {
                condition: () => spaceAbove > containerHeight && spaceToLeft + triggerWidth > containerWidth,
                placement: {
                    top: minY - containerHeight,
                    left: maxX - containerWidth, // Align right edge of dropdown with button right edge
                    origin: 'bottom right',
                },
            },
            {
                condition: () => spaceAbove > containerHeight && spaceToRight + triggerWidth > containerWidth,
                placement: {
                    top: minY - containerHeight, // Position dropdown above button
                    left: minX,
                    origin: 'bottom left',
                },
            },
            {
                condition: () => spaceToLeft >= containerWidth,
                placement: {
                    top: 0,
                    left: minX - containerWidth,
                    origin: spaceBelow >= containerHeight ? 'top right' : 'bottom right',
                },
            },
            {
                condition: () => spaceToRight >= containerWidth,
                placement: {
                    top: 0,
                    left: maxX,
                    origin: spaceBelow >= containerHeight ? 'top left' : 'bottom left',
                },
            },
        ];

        const selectedPlacement = placements.find(({ condition }) => condition());
        if (selectedPlacement) {
            this._calendar.style.setProperty('--calendar-top', `${selectedPlacement.placement.top}px`);
            this._calendar.style.setProperty('--calendar-left', `${selectedPlacement.placement.left}px`);
            this._calendar.style.setProperty('--calendar-transform-origin', selectedPlacement.placement.origin);
        }
    }

    private _calculateSpace(left, top, right, bottom) {
        const windowWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        const windowHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

        const spaceAbove = top;
        const spaceBelow = windowHeight - bottom;
        const spaceToLeft = left;
        const spaceToRight = windowWidth - right;

        return { spaceAbove, spaceBelow, spaceToLeft, spaceToRight };
    }

    private _nowForCalendar() {
        if (this._calendarNow) {
            return this._calendarNow;
        }
        if (this._selectedDate) {
            return this._selectedDate;
        }
        if (this.start) {
            return new Date(this.start);
        }
        return new Date();
    }

    private _onCalendarChange(e) {
        const selectedDate = e.target['value'];
        const currentValue = this.value;
        const newValue = selectedDate ? formatDateString(selectedDate, CalendarType.Date) : '';

        if (currentValue !== newValue) {
            this.blur();
            this.value = newValue;
            this._dispatchChangeEvent();
        }
        this._calendarNow = e.target.now;
    }

    private _onInputFocus(e) {
        const target = e.target;
        if (target && target.value) {
            setTimeout(() => target.select(), 0);
        }
        this.focused = true;
        this._calenderFocused = false;
        this._show = true;
        this._calendar.style.removeProperty('transform');
    }

    private async _onInputBlur() {
        this.focused = false;
        await this.updateComplete;
        this._handleBlur();
    }

    private _onCalendarFocus() {
        this._calenderFocused = true;
    }

    private async _onCalendarBlur() {
        this._calenderFocused = false;
        await this.updateComplete;
        this._handleBlur();
    }

    private _onCalendarPointerDown(e) {
        this._activeElement = e.target;
    }

    private _handleBlur() {
        setTimeout(() => {
            const { shadowRoot } = this;

            if (shadowRoot) {
                const activeElement = shadowRoot.activeElement || this._activeElement;
                this._activeElement = null;
                if (!activeElement) {
                    this._show = false;
                    this._calendar.style.removeProperty('transform');
                }
            }
        }, 0);
    }

    private _pad = (s, n, token = '0') => ('' + s).padStart(n, token);

    private _validateSetValue() {
        const value = this._inputValue;
        const date = value ? new Date(value) : null;
        const isValid = date ? this._isDateValid(date) : true;

        if (isValid && date) {
            this._selectedDate = date;
            this._calendarNow = date;
            this.invalid = null;
            this._day = this._pad(date.getDate(), 2);
            this._month = this._pad(date.getMonth() + 1, 2);
            this._year = this._pad(date.getFullYear(), 4);
        } else if (date) {
            this._selectedDate = null;
            this.invalid = DatePickerTextKey.INVALID;

            if (date) {
                // If date is invalid due to disabled date, but "value" is in
                // a valid date format, reset "calendarNow" to the month of the date.
                //  newStateProps.calendarNow = date.isValid ? date.toJSDate() : new Date();
            }

            this.dispatchEvent(new DatePickerEvent(DatePickerEventType.Invalid, {}));
        } else {
            this._day = '';
            this._month = '';
            this._year = '';
        }
    }

    private _isDateValid(date: Date) {
        const isDisabled = date ? isDateDisabled(date, this._lang, this._customizeDateHandler) : false;
        return date ? date instanceof Date && !isNaN(date.getTime()) && !isDisabled : true;
    }

    private readonly _customizeDateHandler: DatePickerCustomizeDate = (cDate) => {
        let customizeDate;
        if (!this.customizeDate) {
            if (this.endIsSelectedDate) {
                customizeDate = this._customizeDatepickerStartDate;
            } else if (this.startIsSelectedDate) {
                customizeDate = this._customizeDatepickerEndDate;
            }
        } else {
            customizeDate = this.customizeDate;
        }

        const { start, end } = this;
        const dateString = cDate.format(CalendarType.Date);
        const custom = customizeDate ? customizeDate(cDate) : null;
        let disabled = !!custom && custom.disabled;

        if ((start && start > dateString) || (end && end < dateString)) {
            disabled = true;
        }

        return {
            ...custom,
            disabled,
        };
    };

    private readonly _customizeDatepickerStartDate: DatePickerCustomizeDate = (cDate) =>
        this._customizeDatepickerPeriodDate(cDate, 'start');

    private readonly _customizeDatepickerEndDate: DatePickerCustomizeDate = (cDate) =>
        this._customizeDatepickerPeriodDate(cDate, 'end');

    private _customizeDatepickerPeriodDate(cDate, startEnd: 'start' | 'end') {
        const custom = this.customizeDate ? this.customizeDate(cDate) : null;
        const selected = cDate.isSelected();
        const dateString = cDate.format(CalendarType.Date);
        const startDateString = this.endIsSelectedDate ? this.value : this.start;
        const endDateString = this.startIsSelectedDate ? this.value : this.end;
        const hasStartEndDates = !!(startDateString && endDateString);
        const within =
            hasStartEndDates &&
            dateString > startDateString &&
            dateString < endDateString &&
            startDateString !== endDateString;

        const start = !!endDateString && (startEnd === 'start' ? selected : dateString === startDateString);
        const end = !!startDateString && (startEnd === 'end' ? selected : dateString === endDateString);
        const disabled = !!custom && custom.disabled;

        return {
            ...custom,
            disabled,
            __start: start,
            __end: end,
            __within: within,
        };
    }

    private _autoClose = () => {
        if (
            this.calendarPosition === DatePickerCalendarPosition.Fixed ||
            this.calendarPosition === DatePickerCalendarPosition.FixedAuto
        ) {
            this._show = false;
        }
    };

    /** For now just auto close calendar. Some more sophisticated mechanism could be implemented
     * to reposition when for example scrolling.
     */
    private _handleScroll(e) {
        if (e.target !== this) {
            this._autoClose();
        }
    }

    private _handleResize() {
        this._autoClose();
    }

    private _addEventListeners() {
        if (!this._eventsAdded) {
            document.addEventListener('scroll', this._handleScroll, true);
            document.addEventListener('resize', this._handleResize);
            this._eventsAdded = true;
        }
    }

    private _removeEventListeners() {
        if (this._eventsAdded) {
            document.removeEventListener('scroll', this._handleScroll, true);
            document.removeEventListener('resize', this._handleResize);
            this._eventsAdded = false;
        }
    }
}
